.sortable-item{cursor:all-scroll}.sortable-item.dragging{opacity:0.5}.accordion .accordion-item .accordion-title{position:relative;display:flex;align-items:center;cursor:pointer}.accordion .accordion-item .accordion-title i{position:absolute;right:10px;display:flex;align-items:center;content:'';width:0;height:0;border-left:6px solid transparent;border-right:6px solid transparent;border-top:6px solid #000000;transition:transform 0.5s}.accordion .accordion-item .accordion-title i.open{transform:rotate(-180deg)}.accordion .accordion-item .accordion-content{transition:max-height 0.5s;overflow:hidden}
